.page {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #3f3f3f;
}

.label {
  color: #3f3f3f;
  margin: 30px 0 0 30px;
}

@media screen and (max-width: 640px) {
  .page {
    flex-direction: column;
  }
}

@media screen and (max-height: 720px) {
  .label {
    margin-top: 10px;
  }
}
